.background {
  position: fixed;
  left: 0%;
  top: 0%;
  height: 100vh;
  width: 100vw;
  z-index: 2;
  background-color: #121212;
  opacity: 0.5;
}

.hide {
  display: none;
}

.reset {
  display: none;
}

.bold {
  font-weight: bold;
}

.hero__popup {
  position: fixed;
  width: 57.5rem;
  height: 13.1rem;
  background-color: #FFFFFF;
  z-index: 3;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  animation-name: showPopup;
  animation-duration: 1s;
}
@keyframes showPopup {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.hero__popup--alert {
  font-size: 2.5rem;
  position: absolute;
  left: calc(50% - 190px - 175px);
  top: calc(50% - 25px - 24px);
}
.hero__popup--alertInfo {
  font-size: 1rem;
  position: absolute;
  left: calc(50% - 190px - 175px);
  top: calc(50% - 12px + 37px);
}
.hero__popup--icon {
  position: absolute;
  right: 4%;
  top: 25%;
  cursor: pointer;
}